import React from "react";
import classes from "./Tasks.module.css";
import { getDate } from "../../CustomHooks/getDate.js";

const BlockOfTask = ({ value, removeBlock, changeStatus, changeValue }) => {
  return (
    <li className={classes.Task}>
      <div>
        {/* 1 строка */}
      <span className={classes.Task__title}>{value.Title}</span>
      <p className={classes.Task__date}>Дата завершения - {value.Date}</p>
      </div>
       {/* 2 строка */}
      <div className={classes.Task__info}>
        <label id="status">
          <input
            type="checkbox"
            id="status"
            checked={value.active}
            onChange={() => changeStatus(value)}
          />
        </label>

        <label id="value" className={classes.Task__files}>
          <input
            value={value.Desctiption}
            id="value"
            className={classes.Task__files}
            onChange={(event) => changeValue(value, event.target.value)}
          />
        </label>
        <button className={classes.remBut} onClick={() => removeBlock(value)}>
          remove
        </button>
      </div>
        {/* 3 строка */}
      <label id="file" >
        <input type="file" id="value" className={classes.Task__files} />
      </label>
    </li>
  );
};

export default BlockOfTask;
